.container {
  width: 350px;
  height: 350px;
  background-color: rgba(205, 214, 255, .427);
  border-radius: 10px;
  padding: 10px;
  display: none;
  position: fixed;
  bottom: 40px;
  right: 40px;
}

.img {
  cursor: pointer;
  border-radius: 10px;
}

.title {
  font-size: 10px;
}

.descr {
  text-align: center;
  margin-top: 10px;
}

span {
  margin-left: 10px;
  font-size: 20px;
  font-weight: 700;
}

.ads-container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.btn-wrapper {
  text-align: center;
  margin-top: 10px;
}

.btn {
  width: 200px;
  height: 50px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-top: 10px;
  display: none;
  scale: 1;
}

.btn:active {
  scale: .97;
}

